import React from "react";
import Nav from "./components/nav";
import ProfileBanner from "./pages/about/profilePic";
import AboutBox from "./pages/about/about";
import Footer from "./components/footer";
import Projects from "./pages/portfolio/projects"

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  return (
    <Router>
      <div>
        <Nav />

        <Switch>
          <Route path="/my-react-portfolio/" exact>
            <ProfileBanner />
            <AboutBox />
          </Route>

          <Route path="/my-react-portfolio/portfolio" exact>
            <Projects />
          </Route>

          <Route path="/my-react-portfolio/contact" exact>

          </Route>
        </Switch>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
